@media( max-width: 1200px) {
    .menu__item {
        font-size: 12px;
        padding-right: 25px;
        &:last-child {
            border-right: none; } }
    .promo {
        &__header {
            font-size: 49px; }
        &__btn {
            font-size: 20px; } }
    .promo .row {
        margin-left: 0px; }
    .mobile .mobile_item {
        width: 285px; }
    .mobile .mobile_item_plus {
        left: 125px; }
    .car_img {
        margin-left: 97px; } }

@media ( max-width: 992px ) {
    .menu__item {
        font-size: 9px;
        padding-right: 11px;
        font-weight: 700; }
    .subheader__official {
        font-size: 12px; }
    .subheader__phone {
        font-size: 19px; }
    .promo .row {
        margin-left: 0px; }
    .margin .row {
        margin-left: 0px; }
    .mobile .mobile_item {
        width: 215px;
        height: 260px; }
    .mobile .mobile_item_plus {
        left: 94px; }
    .mobile .mobile_item_subtitle {
        padding-top: 55px; }
    .reasons .reasons_block .reasons__descr {
        width: 180px; }
    .car_img {
        margin-left: -30px; }
    .reasons_text {
        width: 340px; }
    .reasons_block {
        flex-direction: column; }
    .reasons__descr {
        margin-top: 30px;
        min-width: 342px;
        text-align: center; }
    .subheader__btn {
        max-width: 100%; } }

@media ( max-width: 769px ) {
    .car_img {
        margin-left: -30px; }
    .mobile .mobile__subtext {
        padding-top: 30px;
        padding-bottom: 40px;
        width: 100%;
        text-align: center; }
    .footer_network {
        top: 10px;
        flex-direction: column;
        row-gap: 8px; }
    .car_subtitle {
        margin-top: 20px; }
    .car_img {
        width: 550px;
        height: 350px; }
    .mobile .mobile__text {
        width: 100%;
        text-align: center;
        height: 100px; }
    .subheader__phone {
        font-size: 15px;
        margin-top: 28px; }
    .subheader__official {
        display: none; }
    .subheader__call {
        display: none; }
    .subheader__logo {
        margin-top: 28px;
        img {
            width: 100%; } }
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        left: -100%;
        top: 0;
        right: 0;
        width: 50%;
        height: 100vh;
        padding: 100px 15px;
        background-color: rgba(0, 0,0, .8);
        transition: 0.6s all;
        &_active {
            left: 0;
            z-index: 10; }
        &__item {
            padding-right: 0;
            border: none;
            margin: 10px 0; }
        &__link {
            font-size: 20px; } }
    .header {
        position: relative;
        padding-top: 10px; }
    .humburger {
        width: 26px;
        margin-top: 11px;
        position: absolute;
        height: 20px;
        top: 15px;
        right: 20px;
        span {
            display: block;
            background-color: #fff;
            width: 26px;
            height: 2px;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-last-child(1) {
                    transform: translateY(-50%) rotate(-45deg); }
                &:nth-last-child(2) {
                    display: none; }
                &:nth-last-child(3) {
                    transform: translateY(-50%) rotate(45deg); } } } } }

@media ( max-width: 576px ) {
    .subheader__phone {
        font-size: 10px; }
    .mobile {
        padding: 20px 0 40px 0;
        .row {
            margin-top: 30px;
            &_item {
                height: 254px;
                padding: 81px 40px 0 40px; } } }
    .car_img {
        margin-top: 100px;
        height: 210px;
        width: 310px; }
    .require_block_descr {
        max-width: 300px; }
    .footer_info {
        width: 300px; }
    .footer_sub {
        width: 250px; } }
