header {
    height: 51px;
    background-color: #212121; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13.5px 70px;
    margin: 0px;
    list-style-type: none; }

.menu__item {
    border-right: 2px dotted black;
    padding-right: 43px;
    &:last-child {
        border-right: none; } }

.menu__link {
    text-decoration: none;
    color: #FFFFFF; }
.humburger {
    span {
        display: none; } }

.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6); }

.subheader__logo {
    display: block;
    margin-top: 16px;
    max-width: 100%; }

.subheader__official {
    color: #FFFFFF;
    font-size: 13px;
    padding-left: 5px; }

.subheader__call {
    margin-top: 15px;
    color: #FFFFFF;
    font-size: 12px;
    text-align: right; }

.subheader__phone {
    display: block;
    color: #FFFFFF;
    text-decoration: none;
    font-size: 22px;
    text-align: right; }

.subheader__btn {
    background-color: #1EACC7;
    text-transform: uppercase;
    margin: 0 auto;
    margin-top: 19px;
    border-radius: 4px;
    width: 172px;
    height: 46px;
    font-size: 14px;
    color: #FFFFFF;
    border: none; }

.promo {
    padding: 152px 0 185px 0;
    min-height: 800px;
    background: url('../img/first__bg.png') center (center / cover) no-repeat; }

.promo__header {
    font-size: 60px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-align: center;
    font-weight: 700px;
    line-height: 60px;
    margin-bottom: 0; }

.promo__subheader {
    margin-top: 23px;
    margin-bottom: 0;
    font-size: 36px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-align: center;
    font-weight: 700px;
    line-height: 36px; }

.promo__descr {
    margin-top: 16px;
    color: #FFFFFF;
    font-size: 18px;
    text-align: center; }

.promo__btn {
    display: block;
    background-color: #1EACC7;
    text-transform: uppercase;
    margin: 116px auto 0 auto;
    border-radius: 4px;
    width: 259px;
    height: 63px;
    font-size: 14px;
    color: #FFFFFF;
    border: none; }

.plases {
    padding: 18px 0 64px 0;
    min-height: 815px;
    background-color: #FFFFFF; }

.plases__btn {
    display: block;
    margin: 0 auto 42px auto;
    height: 24px;
    width: 113px;
    background-color: black;
    font-size: 11px;
    text-transform: uppercase;
    color: #FFFFFF; }

.plases__text {
    text-align: center;
    font-size: 38px;
    text-transform: uppercase;
    color: #222222; }

/* resons */
.reasons {
    padding: 71px 0 65px 0; }

.reasons .row {
    margin-left: 0px; }

.label {
    position: relative;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        background-color: #fff;
        color: #000; }
    &_min {
        width: 76px; } }

.title {
    position: relative;
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        color: #fff; } }

.subtitle {
    position: relative;
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center;
    z-index: 2;
    &_white {
        color: #fff; } }

.reasons .reasons_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px;
    .reasons_round {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: #1EACC7;
        border-radius: 100%; }
    .reasons__descr {
        width: 300px;
        .reasons_subtitle {
            line-height: 21px;
            color: #1EACC7;
            font-size: 17px;
            font-weight: 400; }
        .reasons_text {
            color: #202020;
            font-size: 15px;
            font-weight: 300;
            line-height: 21px; } }

 }    // mobile

.mobile {
    min-height: 865px;
    background: url('../img/mobile_bg.png')center center/cover no-repeat;
    padding: 46px 0 80px 0;
    .row {
        margin: 30px auto 0 auto; }
    .mobile_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        margin-right: 67px; }
    .mobile_item_1 {
        background: url('../img/kart1.png'); }
    .mobile_item_2 {
        background: url('../img/kart2.png'); }
    .mobile_item_3 {
        background: url('../img/kart3.png'); }
    .mobile_item_4 {
        background: url('../img/kart4.png'); }
    .mobile_item_5 {
        background: url('../img/kart5.png'); }
    .mobile_item_6 {
        background: url('../img/kart6.png'); }
    .mobile__btn {
        display: block;
        width: 115px;
        height: 27px;
        background-color: #000000;
        color: #FFFFFF;
        margin: 0 auto;
        text-align: center; }
    .mobile__text {
        height: 48px;
        width: 640px;
        color: #222222;
        font-size: 38px;
        margin: 0 auto;
        margin-top: 33px;
        font-weight: 700;
        text-transform: uppercase; }
    .mobile__subtext {
        font-size: 17px;
        max-width: 825px;
        height: 21px;
        color: #222222;
        margin: 0 auto;
        margin-top: 5px; }
    .mobile_item_subtitle {
        color: #FFFFFF;
        font-size: 24px;
        text-transform: uppercase;
        text-align: center;
        padding: 84px 0 34px 0; }
    .mobile_item_plus {
        background-color: #000000;
        border-radius: 100%;
        width: 29px;
        height: 29px;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 34px;
        left: 145px;
        &::before {
            content: '';
            display: block;
            width: 14px;
            height: 2px;
            background-color: #1EACC7;
            transition: 0.3s all; }
        &::after {
            content: '';
            display: block;
            width: 2px;
            height: 14px;
            background-color: #1EACC7;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            transition: 0.3s all; }
        &:hover {
            &:before, &::after {
                background-color: #e34141; } } }

 }    // car

.car {
    height: 895px;
    position: relative;
    &_btn {
        height: 27px;
