        text-align: center;
        color: #FFFFFF;
        margin-bottom: 0px;
        position: relative; }
    &_subtitle {
        font-size: 17px;
        text-align: center;
        color: #FFFFFF;
        position: relative; } }

.f_part {
    padding-top: 55px;
    background: url('../img/black_bg.png') center center/cover no-repeat;
    height: 500px;
